.App {
  text-align: center;
}

body {
  min-height: 100vh;
  background-color: #282c34;
  /* background-image: url("https://i.pinimg.com/originals/fc/12/4e/fc124e5baaf9be6f25c43b448fef0ad5.gif");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; */
}

#search-bar {
  position: absolute;
  top: 5vh;
  left: 25%;
  width: 50%;
  z-index: 2;
}

#blur {
  display: block;
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgba(40, 44, 52, 0.4);
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: spin 2s linear infinite;
  margin: auto;
  margin-top: 25%;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

#stats-container {
  position: relative;
  top: 15vh;
  width: 50%;
  min-height: 60vh;
  max-height: 60vh;
  left: 25%;
  background-color: #1e1e1e;
  display: flex;
}

@media only screen and (max-width: 900px) {
  #stats-container {
    width: 90%;
    left: 5%;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.name {
  width: fit-content;
  margin: auto;
  margin-top: 2px;
  border: orange 1px solid;
  border-radius: 3px;
  min-width: 10vw;
  display: flex;
  cursor: pointer;
}

.skin-options {
  width: 25%;
  height: 100%;
  padding: 10px;
}

.stats {
  width: 75%;
  overflow-x: visible;
}

.opturn {
  color: orange;
}

.footer {
  position: absolute;
  top: 96vh;
  margin-left: 1vw;
  color: rgba(255, 255, 255, 0.4);
}